*{font-family:Arial, sans-serif;font-weight:normal}.message,.info-message,.error-message{font-size:16px;font-weight:bold}@media (max-width: 600px){.message,.info-message,.error-message{text-align:center}}.info-message{color:#a5a4a4}.error-message{color:#c72a1c}.request-button{font-size:16px;padding:14px 20px;margin:20px 0;border:none;border-radius:4px;font-weight:bold}@media (max-width: 600px){.request-button{width:100%;white-space:normal}}.request-button:disabled{background-color:#9fb3a0;color:#f1efef}.request-button:enabled{background-color:#4caf50;color:#fff;cursor:pointer}.request-button:enabled:hover{background-color:#45a049}#user-list{transition:visibility 0s, opacity 0.5s linear}.user-entry{background-color:#e9e9e9;border-radius:4px;padding:5px 14px;margin:20px 0;border:none}.user-entry .user-header{font-size:15px;font-weight:bold}.user-entry .user-span{font-size:14px}#container{transition:visibility 0s, opacity 0.5s linear}@media (min-width: 600px){#container{width:600px;margin-left:20px}}@media (max-width: 600px){#container{padding-left:20px;padding-right:20px}}@media (min-width: 600px){.spinner{display:inline-block;padding-left:10px;vertical-align:middle;height:100%}}@media (max-width: 600px){.spinner{text-align:center}}.hidden{visibility:hidden;opacity:0}.hidden.spinner{display:none}.shown{visibility:visible;opacity:1}.loader{border-style:solid;border-width:8px;border-color:#dbdbdb;border-top-color:#929292;border-radius:50%;width:24px;height:24px;animation:spin 2s linear infinite;display:inline-block}@media (max-width: 600px){.loader{border-width:12px;width:50px;height:50px}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
